import React, { useState, Component } from "react";
import { Result, Icon, Toast, Button } from "antd-mobile";
import getSearchParams from "@/utils/tools/getSearchParams";
import { QuitSys } from "@/service/http";
import storage from "@/utils/storage";
require("./style.scss");

const ResultPage = (props: any) => {
  const { code, client_token } = getSearchParams(location.search);
  storage.setItem("Authorization", client_token);
  const userToken = storage.getItem("userToken") ? storage.getItem("userToken") : "";
  const getMessage = (code: string | string[]) => {
    if (Array.isArray(code)) code = code[0];
    switch (Number(code)) {
      case 600009:
        return "系统错误，请联系系统管理员";
      case 600017:
        return "该账户已在别处登录";
      default:
        return "系统错误，请联系系统管理员";
    }
  };
  const ToLogin = () => {
    QuitSys(userToken, 0)
      .then((res: any) => {
        if (res.code == 0) {
          window.location.replace(res.result);
        }
      })
      .catch(err => {
        Toast.fail(err.message || "系统出错", 1);
      });
  };

  return (
    <div className="result-page">
      <Result
        img={<Icon type="cross-circle-o" className="spe" size="lg" style={{ fill: code === "600017" ? "rgba(42, 182, 217, 1)" : "#F13642" }} />}
        title={code === "600017" ? "登录失败" : "页面错误"}
        message={getMessage(code)}
      />
      {code == "600017" ? (
        <Button
          type="primary"
          size="large"
          style={{ backgroundColor: "rgba(42, 182, 217, 1)", borderRadius: "20px", margin: "20px" }}
          onClick={ToLogin}
        >
          返回登录
        </Button>
      ) : (
        ""
      )}
    </div>
  );
};

export default ResultPage;
